.loader {
  letter-spacing: 2px;
  color: #000;
  text-shadow: 0 0 1px #3f3b3b;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: bold;
  animation: smokeOut 1s ease-in-out infinite alternate;
}

.loader:before {
  content: "Loading data, please wait...";
}

@keyframes smokeOut {
  100% {
    opacity: .08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

